    --bkg-clr: #2F293D;
    --pink: #FF9595;
    --pink-light: #FFDDDD;
    --pink-dark: #D49C9C;
}

* {
    box-sizing: border-box;
}

body,
header,
nav,
.social {
    display: flex;
}

body {
    margin: 0;
    padding: 1rem;
    background-color: var(--bkg-clr);
    user-select: none;

    font-family: 'Inter';
    color: var(--pink-light);

    flex-direction: column;
}

button,
input,
textarea {
    appearance: none;
    border: none;
    outline: none;
}

button {
    cursor: pointer;
}

header {
    justify-content: space-between;
    font-weight: bold;

    & .logo {
        width: 10rem;
    }

    & nav {
        gap: 1.2rem;
        font-weight: lighter;

        & button {
            background-color: transparent;
            color: var(--pink-light);
        }

        /* to do: use JS because it's buggy this way */
        & button:focus {
            color: var(--pink);
        }
    }
}

footer {

    & .social {
        justify-content: flex-end;
        gap: 0.8rem;

        & img {
            width: 1.5rem;
            height: 1.5rem;
        }
    }
}

#content {
    display: flex;

    padding: 2rem 0;

    & button {
        margin-top: 1rem;
        padding: 0.7rem 1.5rem;
        border-radius: 9px;
        background-color: var(--pink);
        color: white;
        font-weight: bold;
    }

    & button:hover {
        background-color: white;
        color: var(--pink);
    }
}

#content.home {
    margin: 0 1rem;
}

@media only screen and (max-width: 768px) {
    
    body {
        display: flex;
        flex-direction: column;
        gap: 0.7rem;
        padding: 0;
    }

    nav {
        padding: 1rem;
    }

    footer {
        padding: 10%;
        align-self: center;
    }
    
    #content.home {
        flex-direction: column;
        gap: 1rem;

        padding: 2rem;
        margin: 0 auto;

        & img {
            width: 20rem;
        }

        & .welcome {
            display: flex;
            flex-direction: column;
            
            & h1 {
                font-size: 2.5rem;
            }
            
        }

    }

    #content.contact {
        width: 70%;

        & form {
            gap: 1.6rem;

            & input {
                padding: 1rem;
            }
        }
    }

    #content.contact,
    #content.about {
        margin-bottom: 3rem;
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAOA;IACI,kBAAkB;IAClB,eAAe;IACf,qBAAqB;IACrB,oBAAoB;AACxB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;;;;IAII,aAAa;AACjB;;AAEA;IACI,SAAS;IACT,aAAa;IACb,gCAAgC;IAChC,iBAAiB;;IAEjB,oBAAoB;IACpB,wBAAwB;;IAExB,sBAAsB;AAC1B;;AAEA;;;IAGI,gBAAgB;IAChB,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,8BAA8B;IAC9B,iBAAiB;;IAEjB;QACI,YAAY;IAChB;;IAEA;QACI,WAAW;QACX,oBAAoB;;QAEpB;YACI,6BAA6B;YAC7B,wBAAwB;QAC5B;;QAEA,8CAA8C;QAC9C;YACI,kBAAkB;QACtB;IACJ;AACJ;;AAEA;;IAEI;QACI,yBAAyB;QACzB,WAAW;;QAEX;YACI,aAAa;YACb,cAAc;QAClB;IACJ;AACJ;;AAEA;IACI,aAAa;;IAEb,eAAe;;IAEf;QACI,gBAAgB;QAChB,sBAAsB;QACtB,kBAAkB;QAClB,6BAA6B;QAC7B,YAAY;QACZ,iBAAiB;IACrB;;IAEA;QACI,uBAAuB;QACvB,kBAAkB;IACtB;AACJ;;AAEA;IACI,cAAc;AAClB;;AAEA;;IAEI;QACI,aAAa;QACb,sBAAsB;QACtB,WAAW;QACX,UAAU;IACd;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,YAAY;QACZ,kBAAkB;IACtB;;IAEA;QACI,sBAAsB;QACtB,SAAS;;QAET,aAAa;QACb,cAAc;;QAEd;YACI,YAAY;QAChB;;QAEA;YACI,aAAa;YACb,sBAAsB;;YAEtB;gBACI,iBAAiB;YACrB;;QAEJ;;IAEJ;;IAEA;QACI,UAAU;;QAEV;YACI,WAAW;;YAEX;gBACI,aAAa;YACjB;QACJ;IACJ;;IAEA;;QAEI,mBAAmB;IACvB;AACJ","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');\n\n@import './css/home.css';\n@import './css/menu.css';\n@import './css/contact.css';\n@import './css/about.css';\n\n:root {\n    --bkg-clr: #2F293D;\n    --pink: #FF9595;\n    --pink-light: #FFDDDD;\n    --pink-dark: #D49C9C;\n}\n\n* {\n    box-sizing: border-box;\n}\n\nbody,\nheader,\nnav,\n.social {\n    display: flex;\n}\n\nbody {\n    margin: 0;\n    padding: 1rem;\n    background-color: var(--bkg-clr);\n    user-select: none;\n\n    font-family: 'Inter';\n    color: var(--pink-light);\n\n    flex-direction: column;\n}\n\nbutton,\ninput,\ntextarea {\n    appearance: none;\n    border: none;\n    outline: none;\n}\n\nbutton {\n    cursor: pointer;\n}\n\nheader {\n    justify-content: space-between;\n    font-weight: bold;\n\n    & .logo {\n        